.mevieDispaly{
    display: flex;
    flex-direction: column;
    box-shadow: 0px 0px 5px 0px;
    border-radius: 10px;
}
.entire_moviedetails{
    display: flex;
    flex-wrap: wrap;
    column-gap: 80px;
    row-gap: 30px;
    margin-left: 50px;
    margin-top: 40px;
}
.movies_imageDisply{
    width: 300px;
    height: 350px;
    border-radius: 10px;
    cursor: pointer;
}
.movieDispaly_title{
    padding-left: 20px;
    width: 250px;
}
.movieDispaly_releasedate{
    padding-left: 20px;
}
.button_divs{
    margin-top: 80px;
    margin-left: 50px;
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 50px;
}
.button_rows{
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 30px;
    border: 2px solid black;
    border-radius: 50px;
}
.subcategory_btn{
    background-color: white;
    border-style: none;
    border-radius: 50px;
    font-size: large;
    width: 120px;
    height: 40px;
    cursor: pointer;
}
.subcategory_btn:hover{
    background-color: #032541;
    color: white;
}